export default {
	computed: {
		showNoResul () {
			return !this.$store.state.qsList.length && this.$store.state.typing
		}
	},
	data() {
		return {
			showToTop: false,
			scrollTop: 0,
			old: {
				scrollTop: 0
			}
		}
	},
	methods: {
		goTopHandle() {
			// 回到顶部
			this.scrollTop = 0
			this.scrollTop = this.old.scrollTop
			this.$nextTick(() => {
				this.scrollTop = 0
			})
		},
		scrollHandle(event) {
			this.old.scrollTop = event.detail.scrollTop
			if(event.detail.scrollTop > 666) {
				this.showToTop = true
			} else {
				this.showToTop = false
			}
		}
	},
	 onLoad: async function() {
		uni.showLoading({
			title: '加载中'
		})
		this.$store.commit('typing',false) // 请求结束后
		this.$store.commit('qsList', [])
		await this.$store.dispatch("qsList")
		uni.hideLoading()
	}
}
